/* TwoColumnLayout.css */

.container {
    display: flex;
  }
  
  .left-column {
    flex: 1;
    position: relative;
    background-color: #f0f0f0;
  }
  
  .background-image1 {
    background-image: url('../../../public/Emma\'s\ family\ feasts_\ Cornish\ pasties.jpeg'); /* Replace with your image path */
    background-attachment: fixed;
    padding:35% 7.5% 35% 7.5%;
    position:top;
    background-repeat: no-repeat;
  }
  
  .right-column {
    flex: 1;
    padding: 20px;
    background-color: #fff;
  }
  @media (max-width: 1268px) {
    .background-image1 {
      background-image: url('../../../public/Emma\'s\ family\ feasts_\ Cornish\ pasties.jpeg'); /* Replace with your image path */
      background-attachment: fixed;
      background-position: bottom right;
      background-repeat: no-repeat;
    }
    .container {
      display: flex;
      flex-direction: column;
    }
    .pastries {
      padding: 20px;
    }
    
    .left-column {
      flex:1;
      position: relative;
      background-color: #f0f0f0;
    }
    .right-column {
      flex:0.8;
      padding: 20px;
      background-color: #fff;
    }
  }