/* Header.css */

.sticky-header {
  position: fixed;
  width:100%;
  top: 0;
  background-color: #333;
  color: #fff;
  padding: 10px 20px;
  z-index: 100; /* Adjust the z-index as needed */
}

nav li {
  margin-right: 20px;
}

/* Add more styles as needed */


nav ul {
  list-style-type: none;
  margin: 0;
  padding: 0;
  text-align: center;
}

nav li {
  display: inline;
  margin-right: 20px;
}

nav a {
  color: #fff;
  text-decoration: none;
  font-weight: bold;
  font-size: 16px;
}
/* Add more styles as needed */
