/* Add this CSS to your stylesheet (e.g., ResponsiveStyles.css) */

/* Default styles for larger screens */
.contact {
    margin: 40px auto;
    max-width: 800px;
    padding: 20px;
  }
  
  .contact h2 {
    font-size: 24px;
  }
  
  .contact-info {
    display: flex;
    flex-wrap: wrap;
    gap: 20px;
  }
  
  .contact-details {
    flex: 1;
  }
  
  .contact-details p {
    font-size: 16px;
  }
  
  .map {
    flex: 1;
  }
  
  /* Media query for screens up to 768px wide (typical mobile screens) */
  @media (max-width: 768px) {
    .contact {
      padding: 10px;
    }
  
    .contact h2 {
      font-size: 20px;
    }
  
    .contact-info {
      flex-direction: column;
      gap: 10px;
    }
  
    .map {
      order: -1; /* Move the map to the top for better mobile layout */
    }
  }
  