.App {
  text-align: center;
}


/* Reset some default styles */
body {
  margin: 0;
  padding: 0;
  font-family: Arial, sans-serif;
}

/* Header styles */


/* Main content styles */
main {
  padding: 20px;
}

section {
  margin-bottom: 40px;
}

h1, h2 {
  color: #333;
}

/* Add more styles for other sections, such as #pastries, #coffee-bubble-tea, #cakes, and #contact */

/* Social icons */
.social-icons {
  margin-top: 20px;
}

.social-icons a {
  text-decoration: none;
  color: #333;
  margin: 0 10px;
  font-size: 24px;
}

/* Responsive styles (adjust as needed) */
@media (max-width: 768px) {
  header {
    padding: 10px;
  }

  nav ul {
    text-align: center;
  }

  nav li {
    display: block;
    margin: 10px 0;
  }

  /* Add more responsive styles as needed */
}
