.home-section {
    background-image: url('../../../public/351179490_225605466922342_5637203866612877333_n.jpg');
    background-size: cover; /* Adjust as needed */
    background-position: top; /* Adjust as needed */
    text-align: center;
    background-attachment: fixed;
    background-repeat: no-repeat;
    padding:7.5% 0 7.5% 0;
  }
  #home {
    background-color: #f8f8f8;
    text-align: center;
    padding: 0px;
  }
  
  .text-content {
    width:50%;
    padding:15% 5% 15% 5%;
    margin-left: 25%;
    position: center; /* Add spacing as needed */
    z-index: 1; 
    border-style:solid;
    border-radius:5%;
    color:white;
    border-color: #ffffff;
    background-color: rgba(0, 0, 0, 0.5);
    font-weight: bolder;
    /* Place text content above the background image */
  }
  .text-content h1{
    font-weight: bolder;
    color:white;
  }
/* Media query for screens up to 768px wide (typical mobile screens) */
@media (max-width: 1268px) {
  .home {
    padding: 20px;
  }

  .home h1 {
    font-size: 28px;
    margin-bottom: 10px;
  }

  .home p {
    font-size: 16px;
  }
}
