
.container1 {
    display: flex;
    flex-direction: row-reverse;
  }
  
  .left-column1 {
    flex: 1;
    position: relative;
    background-color: #f0f0f0;
  }
  
  .background-image2 {
    background-image: url('../../../public/Premium\ Photo\ _\ Taiwan\ milk\ tea\ with\ bubble.jpeg'); /* Replace with your image path */
    background-attachment: fixed;
    padding:35% 7.5% 35% 7.5%;
    background-position: center right;
    background-repeat: no-repeat;
  }
  
  .right-column1 {
    flex: 1;
    padding: 20px;
    background-color: #fff;
  }
  @media (max-width: 1268px) {
    .background-image2 {
      background-image: url('../../../public/Premium\ Photo\ _\ Taiwan\ milk\ tea\ with\ bubble.jpeg'); /* Replace with your image path */
      background-attachment: fixed;
      background-position: bottom right;
      background-repeat: no-repeat;
    }
    .container1 {
      display: flex;
      flex-direction: column;
    }

    .left-column1 {
      flex:1;
      position: relative;
      background-color: #f0f0f0;
    }
    .right-column1 {
      flex:0.8;
      padding: 20px;
      background-color: #fff;
    }
  }